const updateCartItems = (cartItems, item, index) => {
  if (item.count === 0) {
    return [...cartItems.slice(0, index), ...cartItems.slice(index + 1)]
  }

  if (index === -1) {
    return [...cartItems, item]
  }

  return [...cartItems.slice(0, index), item, ...cartItems.slice(index + 1)]
}

const updateCartItem = (book, item = {}, quantity) => {
  const { id = book.id, count = 0, title = book.title, total = 0 } = item

  return {
    id,
    title,
    count: count + quantity,
    total:
      Math.round((total + quantity * book.price + Number.EPSILON) * 100) / 100,
  }
}

const updateOrder = (state, bookId, quantity) => {
  const {
    booksList: { books },
    cart: { cartItems },
  } = state
  const book = books.find(({ id }) => id === bookId)
  const itemIndex = cartItems.findIndex(({ id }) => id === bookId)
  const item = cartItems[itemIndex]

  const newItem = updateCartItem(book, item, quantity)
  const updatedCartItems = updateCartItems(cartItems, newItem, itemIndex)
  let updatedOrderTotal = 0
  let updatedItemsTotal = 0
  
  updatedCartItems.map(item => {
    updatedOrderTotal += item.total
    updatedItemsTotal += item.count
    return null
  })

  return {
    cartItems: updatedCartItems,
    orderTotal: Math.round((updatedOrderTotal + Number.EPSILON) * 100) / 100,
    itemsTotal: updatedItemsTotal,
  }
}

const updateCart = (state, action) => {
  if (state === undefined) {
    return {
      cartItems: [],
      orderTotal: 0,
      itemsTotal: 0,
    }
  }

  switch (action.type) {
    case 'BOOK_ADDED_TO_CART':
      return updateOrder(state, action.payload, 1)

    case 'BOOK_REMOVED_FROM_CART':
      return updateOrder(state, action.payload, -1)

    case 'ALL_BOOKS_REMOVED_FROM_CART':
      const item = state.cart.cartItems.find(({ id }) => id === action.payload)
      return updateOrder(state, action.payload, -item.count)
    default:
      return state.cart
  }
}

export default updateCart
